@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@300;500&display=swap");
body, html {
  margin: 0;
  overflow: hidden;
  -webkit-transition: opacity 400ms;
  -moz-transition: opacity 400ms;
  transition: opacity 400ms;
}

body, .onepage-wrapper, html {
  display: block;
  position: static;
  padding: 0;
  width: 100%;
  height: 100%;
}

.onepage-wrapper {
  width: 100%;
  height: 100%;
  display: block;
  position: relative;
  padding: 0;
  -webkit-transform-style: preserve-3d;
}

.onepage-wrapper .section {
  width: 100%;
  height: 100%;
}

.onepage-pagination {
  position: absolute;
  right: 10px;
  top: 50%;
  z-index: 5;
  list-style: none;
  margin: 0;
  padding: 0;
}

.onepage-pagination li {
  padding: 0;
  text-align: center;
}

.onepage-pagination li a {
  margin: 20px 10px;
  width: 16px;
  height: 16px;
  display: block;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.13);
  box-shadow: 0 0 0 0px rgba(255, 255, 255, 0);
  transition: all 0.3s;
}

.onepage-pagination li a.active {
  box-shadow: 0 0 0 10px rgba(255, 255, 255, 0.13);
  background: white;
}

.disabled-onepage-scroll, .disabled-onepage-scroll .wrapper {
  overflow: auto;
}

.disabled-onepage-scroll .onepage-wrapper .section {
  position: relative !important;
  top: auto !important;
  left: auto !important;
}

.disabled-onepage-scroll .onepage-wrapper {
  -webkit-transform: none !important;
  -moz-transform: none !important;
  transform: none !important;
  -ms-transform: none !important;
  min-height: 100%;
}

.disabled-onepage-scroll .onepage-pagination {
  display: none;
}

body.disabled-onepage-scroll, .disabled-onepage-scroll .onepage-wrapper, html {
  position: inherit;
}

* {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -o-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  outline: none !important;
  margin: 0;
  padding: 0;
}

ul, li {
  list-style-type: none;
  margin: 0;
}

img {
  max-width: 100%;
}

p + p {
  margin-top: 15px;
}

body {
  font-family: "Raleway", sans-serif;
  color: #231f20;
  -webkit-font-smoothing: antialiased;
  -webkit-font-variant-ligatures: no-common-ligatures;
  font-variant-ligatures: no-common-ligatures;
  overflow-x: hidden;
}

.wrapper {
  float: left;
  width: 100%;
  position: relative;
}

#jpreOverlay {
  background-color: #000;
}

#jpreLoader {
  width: 100%;
  height: 3px;
}

#jpreBar {
  background: #fff;
}

#jprePercentage {
  display: none;
}

#shetterTop, #shetterBottom {
  width: 100%;
  z-index: 555555;
  height: 50%;
  background: #000;
  position: fixed;
}

#shetterTop {
  top: 0;
}

#shetterBottom {
  bottom: 0;
}

.container {
  max-width: 1100px;
  width: 98%;
  margin: 0 auto;
  padding: 0 10px;
}

p {
  font-size: 17px;
}

h2 {
  font-size: 34px;
  margin-bottom: 15px;
  font-weight: normal;
}
@media (max-width: 768px) {
  h2 {
    font-size: 28px;
  }
}

.leftMenu {
  width: 50px;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background: #073c3c;
  z-index: 9999999;
}
.leftMenu #menu {
  width: 80vh;
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  /* height: 100%; */
  transform: rotate(-90deg);
  transform-origin: bottom left;
  position: absolute;
  bottom: 0;
  left: 33px;
}
.leftMenu #menu li {
  display: block;
  position: relative;
}
.leftMenu #menu li a {
  font-size: 17px;
  color: #9bd3d3;
  padding: 15px 20px;
  width: 100%;
  text-decoration: none;
  white-space: nowrap;
}
.leftMenu #menu li a.active {
  color: #e49028;
}

.logowrap {
  width: 100%;
  padding: 10px 3px;
  background: fixed;
  background: #e49028;
  display: block;
  line-height: 0;
}
.logowrap img {
  width: 100%;
}

.main {
  width: 50%;
  float: right;
}
.main .section {
  background: #073c3c;
}

.leftSection {
  width: 50%;
  height: 100%;
  padding-left: 50px;
  float: left;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.leftSection .mianlogowarap {
  width: 50%;
  display: block;
  text-align: center;
}
.leftSection .mianlogowarap .mlogo {
  width: 50%;
}
.leftSection .mianlogowarap .namelogo {
  width: 100%;
}
.leftSection .wrapsecin {
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 0 25px;
  width: 100%;
}
.leftSection #countdown-gampang {
  width: 100%;
}

#countdown-gampang {
  width: 100%;
  margin: 6vh 0;
}

.ClassyCountdown-wrapper {
  display: flex;
  justify-content: center;
}
.ClassyCountdown-wrapper > div {
  margin: 20px;
  position: relative;
  height: 136px;
  width: 136px;
}
.ClassyCountdown-wrapper > div .ClassyCountdown-value {
  position: absolute;
  top: 50%;
  left: 50%;
  font-family: sans-serif;
  transform: translate(-50%, -50%);
  margin-top: 0 !important;
  line-height: 1;
  text-align: center;
  line-height: 0.6;
}
.ClassyCountdown-wrapper > div .ClassyCountdown-value span {
  font-size: 16px !important;
}

.rightsections {
  color: #fff;
  text-align: center;
}

.comingSoonSection {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 0 20px;
}
.comingSoonSection h1 {
  font-size: 105px;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 18px;
}
.comingSoonSection h2 {
  font-size: 41px;
  text-transform: uppercase;
  margin: 30px 0;
}
.comingSoonSection p {
  font-size: 23px;
  color: #94b5b5;
}

.subscribeBox {
  display: flex;
  max-width: 600px;
  margin: 30px auto 0;
}
.subscribeBox .emailwrap {
  background: transparent;
  border: 0;
  color: #fff;
  width: 100%;
  padding: 20px;
  border-bottom: solid 1px #117e89;
  font-size: 22px;
}
.subscribeBox .emailwrap::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #117e89;
  opacity: 1;
  /* Firefox */
}
.subscribeBox .emailwrap:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: #117e89;
}
.subscribeBox .emailwrap::-ms-input-placeholder {
  /* Microsoft Edge */
  color: #117e89;
}
.subscribeBox .submitbtn {
  background: transparent;
  border: solid 1px #117e89;
  white-space: nowrap;
  padding: 20px 40px;
  font-size: 16px;
  color: #fff;
  display: block;
  border-radius: 50px;
  cursor: pointer;
  width: auto;
}

.socialIcons {
  display: flex;
  justify-content: center;
}
.socialIcons li {
  padding: 10px;
}
.socialIcons li img {
  width: 26px;
}

.contactFormwrap {
  display: flex;
  flex-wrap: wrap;
  margin: 20px 0;
}
.contactFormwrap li {
  width: 100%;
  padding: 15px;
}
.contactFormwrap li:first-child, .contactFormwrap li:nth-child(2) {
  width: 50%;
}
.contactFormwrap li .submitbtn {
  background: transparent;
  width: auto;
  border: solid 1px #117e89;
  white-space: nowrap;
  padding: 20px 40px;
  font-size: 16px;
  color: #fff;
  display: block;
  border-radius: 50px;
  cursor: pointer;
}
.contactFormwrap input, .contactFormwrap textarea {
  background: transparent;
  border: 0;
  color: #fff;
  width: 100%;
  padding: 10px;
  border-bottom: solid 1px #117e89;
  font-size: 18px;
}
.contactFormwrap input::placeholder, .contactFormwrap textarea::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #117e89;
  opacity: 1;
  /* Firefox */
}
.contactFormwrap input:-ms-input-placeholder, .contactFormwrap textarea:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: #117e89;
}
.contactFormwrap input::-ms-input-placeholder, .contactFormwrap textarea::-ms-input-placeholder {
  /* Microsoft Edge */
  color: #117e89;
}